.main {

    &__brand-baner {
        margin: 19px 17px 0 16px;

        & .banner__wrapper {
            padding: 17px 17px 23px 17px;
        }
    }

    &__filter-buttons {
        margin-top: 28px;

        & .filter-buttons__btn {
            font-size: 12px;
            color: #3075FC;
        }
    }

    &__products-list {
        margin: 0 14px;
    }

    &__reviews-slider-wrapper {
        margin-top: 46px;
    }
}



@media (min-width: $wide-breakpoint) {
    .main {

        &__block-title,
        &__filter-buttons,
        &__products-list,
        &__brand-baner,
        &__reviews-slider-wrapper {
            margin-left: 0;
            margin-right: 0;
            padding-left: 100px;
            padding-right: 100px;
        }

        &__products-list {
            margin-top: 30px;
        }

        &__feedback-sections {
            margin-top: 69px;
        }

        &__filter-buttons {
    
            & .filter-buttons__btn {
                font-size: 16px;
            }
        }
    }
}