.reviews-slider-wrapper {
    display: flex;
    flex-direction: column;
    gap: 28px;

    padding: 19px 0 30px 0;

    background-color: #F5F6F6;

    & .block-title {
        margin: 0 15px;
    }

    &__reviews-slider {
        padding: 0 15px;
        overflow-y: unset;

        & .swiper-slide {
            width: calc(340px + (213 + 213 * 0.7) * ((100vw - 430px) / $hd-plus-width));
            height: auto;
            min-height: 225px;

            margin-right: 10px;

            border-radius: 10px;
            box-shadow: 0px 10px 15px 0px #0000001A;


            & .reviews-wrapper__review {
                height: 100%;
            }

            & .reviews-wrapper__user-text {
                margin-bottom: 0;
                padding-bottom: 17px;
            }

            & .reviews-wrapper__review {
                box-shadow: none;
            }
        }
    }

    &__links-wrapper {
        display: flex;
        gap: 5px;

        margin: 0 15px;
    }

    &__link {
        width: 100%;

        padding: 16px 5px;

        color: #3075FC;
        text-transform: uppercase;
        text-align: center;

        border: 1px solid #3075FC;
        border-radius: 6px;
    }

    &__link--highlight {
        color: $white;
        background-color: #3075FC;
    }
}



@media (min-width: $tablet-breakpoint) {
    .reviews-slider-wrapper {

        &__reviews-slider {

            & .swiper-slide {
                width: calc(340px + 213 * (100vw / $hd-plus-width));
                min-height: 291px;
            }
        }
    }
}

@media (min-width: $wide-breakpoint) {
    .reviews-slider-wrapper {
        display: grid;
        grid-template-columns: 1fr 1fr;

        overflow: hidden;

        background-color: unset;

        &>.block-title,
        &__reviews-slider {
            margin: 0;
            padding: 0;
        }

        &>.block-title {
            display: flex;
            align-items: baseline;
            justify-content: flex-start;
            gap: 18px;

            &>div {
                font-size: 18px;
            }
        }

        &__reviews-slider {
            grid-column: 1 / 3;
            overflow: unset;
        }

        &__links-wrapper {
            grid-row: 1 / 1;
            grid-column: 2 / 3;

            justify-content: flex-end;

            &__link {
                width: auto;
                padding-left: 30px;
                padding-right: 30px;
            }
        }
    }
}